.authenticate-login {
  flex-direction: column;
  gap: .5rem;
  display: flex;

  & > label {
    gap: .3rem;
    display: flex;

    & > span:first-child {
      text-align: right;
      flex: 1 2 50%;
    }

    & > span:nth-child(2) {
      text-align: left;
      flex: 50%;
    }
  }

  & input {
    width: 8rem;
    font-size: 100%;
  }

  & > button {
    align-self: center;
  }
}

.authenticate-logout {
  align-items: center;
  gap: .3rem;
  margin-bottom: .5rem;
  display: flex;
}

.with-socket {
  flex-direction: column;
  align-items: stretch;
  gap: .3rem;
  margin-bottom: .5rem;
  display: flex;

  & .with-socket-control {
    flex-direction: row;
    justify-content: flex-start;
    gap: .3rem;
    display: flex;
  }

  & .with-socket-control span:before {
    content: "[";
  }

  & .with-socket-control span:after {
    content: "]";
  }

  & .with-socket-disconnected {
    color: hsl(var(--error-hue), 100%, 40%);
  }
}

.paint {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: .3rem;
  display: flex;

  & canvas {
    border: 1px dotted gray;
  }

  & .paint-control {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: .3rem;
    display: flex;

    & label {
      gap: .1rem;
      display: flex;
    }

    & input[type="number"] {
      width: 3rem;
    }
  }
}

.text-chat {
  flex-direction: column;
  gap: .3rem;
  display: flex;
}

.text-chat-input {
  justify-content: space-between;
  gap: .3rem;
  display: flex;
}

.text-chat-input select {
  flex: 0 auto;
}

.text-chat-input input {
  flex: auto;
}

.text-chat-input button {
  flex: 0 auto;
}

.text-chat-message-list-container {
  border: 1px dotted gray;
  height: 12rem;
  overflow: auto;
}

.text-chat-message-list {
  flex-direction: column;
  justify-content: flex-end;
  min-height: 100%;
  padding: 0 .2rem;
  display: flex;
}

.text-chat-from-me {
  justify-content: flex-end;
  display: flex;
}

.text-chat-from-them {
  justify-content: flex-start;
  display: flex;
}

.text-chat-message {
  border-radius: .3rem;
  max-width: 80%;
  margin: .2rem 0;
  padding: .5rem;
  position: relative;
  box-shadow: 0 2px 4px #0000001a;
}

.text-chat-from-me .text-chat-message {
  background-color: #e1f3fb;
}

.text-chat-from-them .text-chat-message {
  background-color: #fff;
}

.text-chat-from-me .text-chat-message:before {
  content: "";
  border-top: 10px solid #0000;
  border-bottom: 10px solid #0000;
  border-left: 10px solid #e1f3fb;
  width: 0;
  height: 0;
  position: absolute;
  top: 10px;
  right: -10px;
}

.text-chat-from-them .text-chat-message:before {
  content: "";
  border-top: 10px solid #0000;
  border-bottom: 10px solid #0000;
  border-right: 10px solid #fff;
  width: 0;
  height: 0;
  position: absolute;
  top: 10px;
  left: -10px;
}

.text-chat .text-chat-message-list-container .text-chat-message-list img {
  border: 1px dotted gray;
  height: 2rem;
  margin-top: .3rem;
}
